import {
  FormContainer,
  FormInput,
  FormLabelP,
  FormLine,
  FormShape,
  FormTitle,
  SubmitBtn,
} from "../css/styledForm";
import { useForm } from "react-hook-form";
import MemberService from "../services/MemberService";
import AlertSpan from "../components/AlertSpan";
import Swal from "sweetalert2";
import { useHistory } from "react-router-dom";
import { useRecoilValue } from "recoil";
import { saltKey } from "../atoms";
import IMember from "../model/IMember";

interface ICreateMemberForm {
  id: string;
  password: string;
  passwordCheck: string;
  name: string;
  birth: Date;
  gender: string;
  email: string;
  phoneNumber: string;
}

function SignUp() {
  const {
    register,
    handleSubmit,
    setError,
    formState: {errors},
  } = useForm<ICreateMemberForm>();

  const history = useHistory();
  const salt = useRecoilValue(saltKey);

  const onValid = async ({
                           id,
                           password,
                           passwordCheck,
                           name,
                           birth,
                           gender,
                           email,
                           phoneNumber,
                         }: ICreateMemberForm) => {
    let exist;

    try {
      exist = await MemberService.getMemberById(id);
    } catch (err) {
    }
    if (exist !== undefined) {
      setError(
        "id",
        {message: "  ."},
        {shouldFocus: true}
      );

      return;
    }

    if (password !== passwordCheck) {
      setError(
        "passwordCheck",
        {message: "  ."},
        {shouldFocus: true}
      );

      return;
    }
    let isMale: boolean;

    if (gender === "male") isMale = true;
    else isMale = false;

    let CryptoJS = require("crypto-js");
    let hash = CryptoJS.AES.encrypt(password, salt).toString();

    let member: IMember = {
      id,
      password: hash,
      name,
      birth,
      isMale,
      email,
      phoneNumber,
      isAdmin: false,
    };

    console.log(member);
    await MemberService.createMember(member);

    await Swal.fire({
      icon: "success",
      title: " .",
    });

    history.push("/logIn");
  };

  return (
    <FormContainer>
      <FormShape onSubmit={handleSubmit(onValid)} style={{height: "auto"}}>
        <FormTitle>Sign Up</FormTitle>

        <FormLine>
          <FormLabelP htmlFor="id">ID</FormLabelP>
          <FormInput
            type="text"
            id={"id"}
            {...register("id", {required: " ."})}
          />
        </FormLine>
        {errors?.id?.message ? <AlertSpan msg={errors.id.message}/> : ""}

        <FormLine>
          <FormLabelP htmlFor="password" style={{right: "43%"}}>
            Password
          </FormLabelP>
          <FormInput
            type="password"
            autoComplete="on"
            id={"password"}
            {...register("password", {
              required: " .",
              pattern: {
                value: /^(?=.*[A-Za-z])(?=.*\d)[A-Za-z\d]{8,}$/,
                message:
                  " 8 ,       .",
              },
            })}
          />
        </FormLine>
        {errors?.password?.message ? (
          <AlertSpan msg={errors.password.message}/>
        ) : (
          ""
        )}

        <FormLine>
          <FormLabelP htmlFor="passwordCheck" style={{right: "43%"}}>
            Password Check
          </FormLabelP>
          <FormInput
            type="password"
            autoComplete="on"
            id={"passwordCheck"}
            {...register("passwordCheck", {
              required: "  .",
            })}
          />
        </FormLine>
        {errors?.passwordCheck?.message ? (
          <AlertSpan msg={errors.passwordCheck.message}/>
        ) : (
          ""
        )}

        <FormLine>
          <FormLabelP htmlFor="name">Name</FormLabelP>
          <FormInput
            type="text"
            id={"name"}
            {...register("name", {required: " ."})}
          />
        </FormLine>
        {errors?.name?.message ? <AlertSpan msg={errors.name.message}/> : ""}

        <FormLine>
          <FormLabelP htmlFor="birth" style={{right: "43%"}}>
            Birth Date
          </FormLabelP>
          <FormInput
            type="date"
            id={"birth"}
            {...register("birth", {required: " ."})}
          />
        </FormLine>
        {errors?.birth?.message ? <AlertSpan msg={errors.birth.message}/> : ""}

        <FormLine style={{marginTop: "50px"}}>
          <div>
            <FormLabelP htmlFor="male" style={{top: "0", right: "39%"}}>
              Male
            </FormLabelP>
            <FormInput
              type="radio"
              id="male"
              value="male"
              {...register("gender", {required: true})}
              style={{
                position: "relative",
                right: "-46.8%",
                width: "100px",
                margin: "10px 0",
              }}
            />
          </div>
          <div>
            <FormLabelP htmlFor="female" style={{top: "0", right: "38%"}}>
              Female
            </FormLabelP>
            <FormInput
              type="radio"
              id="female"
              value="female"
              {...register("gender", {required: true})}
              style={{
                position: "relative",
                right: "-46%",
                width: "100px",
              }}
            />
          </div>
        </FormLine>
        {errors?.gender?.message ? (
          <AlertSpan msg={errors.gender.message}/>
        ) : (
          ""
        )}

        <FormLine>
          <FormLabelP htmlFor="email">E-mail</FormLabelP>
          <FormInput
            type="email"
            autoComplete="on"
            id={"email"}
            {...register("email", {
              required: " .",
              pattern: {
                value:
                  /^[0-9a-zA-Z]([-_.]?[0-9a-zA-Z])*@[0-9a-zA-Z]([-_.]?[0-9a-zA-Z])*.[a-zA-Z]{2,3}$/i,
                message: "   .",
              },
            })}
          />
        </FormLine>
        {errors?.email?.message ? <AlertSpan msg={errors.email.message}/> : ""}

        <FormLine>
          <FormLabelP htmlFor="phoneNumber" style={{right: "33%"}}>
            Phone Number
          </FormLabelP>
          <FormLabelP
            htmlFor="phoneNumber"
            style={{right: "-35%", top: "30px"}}
          >
            ex. 010-0000-0000
          </FormLabelP>
          <FormInput
            type="tel"
            id={"phoneNumber"}
            {...register("phoneNumber", {
              required: " .",
              pattern: {
                value: /[0-9]{3}-[0-9]{4}-[0-9]{4}/,
                message: "   .",
              },
            })}
          />
        </FormLine>
        {errors?.phoneNumber?.message ? (
          <AlertSpan msg={errors.phoneNumber.message}/>
        ) : (
          ""
        )}

        <SubmitBtn value="Sign Up"/>
      </FormShape>
    </FormContainer>
  );
}

export default SignUp;
